      console.log(this.result);
      this.score = Object.values(this.result).reduce((acc, c) => {
        return acc + c * 1;
      }, 0);
    },
  },
  computed: {
    completeLength: function () {
      return Object.keys(this.result).length;
    },
  },
};
</script>

